import React from 'react';
import './App.css';

import Header from './Components/Header';
import ListUsers from './Components/ListUsers';
import ViewProfile from './Components/ViewProfile';

class App extends React.Component {

  constructor() {
    super();
    this.state = {
      clickedUser: {},
      users: [
        {
          id: 1,
          email: "jaydon_gislason59@yahoo.com",
          phone: "(998) 970-2799",
          address: {
            city: "South Nyah",
            state: "New York",
            country: "Equatorial Guinea",
            zipCode: "16308-9260",
            countryCode: "AX",
            streetAddress: "3585 Lucinda Valleys"
          },
          lastName: "Von",
          firstName: "Walter",
          profileImage: "https://s3.amazonaws.com/uifaces/faces/twitter/jeremyworboys/128.jpg"
        }
      ]
    }
    this.clickHandler = this.clickHandler.bind(this);
  }

  clickHandler(user) {
    this.setState({
      clickedUser: user
    });
  }

  resetClickedUser = () => {
    this.setState({
      clickedUser : {}
    })
  }

  componentDidMount() {
    
    fetch('https://mock-io.herokuapp.com/users')
      .then(response => response.json())
      .then(data => {this.setState({
        users: data
      })})
  }

  render() {
    return (
      <div className="App">
        <Header />
        <ListUsers users={this.state.users} clickHandler={this.clickHandler}/>
        {this.state.clickedUser.hasOwnProperty('firstName') ? <ViewProfile reset={this.resetClickedUser} user={this.state.clickedUser}/> : <React.Fragment />}
      </div>
    );
  }
}

export default App;
