import React from 'react'

export default function ListUsers(props) {
  return (
    <div className='people-list'>
      <ul>
        {props.users.map((value) =>
          <li style={listItemStyle} key={value.id} onClick={(e) => { props.clickHandler(value) }}>
            <img style={imageStyle} src={value.profileImage} alt=' '></img>
            <span style={spanStyle}>{value.firstName + ' ' + value.lastName}</span>
          </li>
        )}
      </ul>
    </div>
  )
}

let listItemStyle = {
  display: 'flex',
  lineHeight: '50px',
  margin: '10px',
  padding:'5px',
  cursor:'pointer',
  borderRadius:'10px',
  color:'#445',
  boxShadow:'0px 0px 2px grey'
}

const imageStyle = {
  height: '50px',
  borderRadius: '50%',
  border: '2px solid green'
}

const spanStyle = {
  display: 'block',
  lineHeight: '50px',
  marginLeft: '20px'
}
