import React from 'react'
import backImage from './Images/left-arrow.png';

export default class ViewProfile extends React.Component {

  constructor() {
    super();
    this.state = {
      top: window.pageYOffset + window.screen.height,
    }
  }

  disableScroll() {
    let left = window.pageYOffset;
    let top = window.pageXOffset;

    window.onscroll = () => {
      window.scrollTo(top,left);
    }
  }

  getPopUpStyle() {
    return {
      margin: '5px',
      backgroundColor: '#37383890',
      borderRadius: '10px',
      boxShadow: '0px 0px 2px grey',
      width: 'calc(100% - 20px)',
      height: 'calc(100% - 20px)',
      position: 'absolute',
      left: '0',
      top: this.state.top
    }
  }

  componentDidMount() {
    this.disableScroll();
    let id = setInterval(() => {
      this.setState({
        top : this.state.top - 20
      });
      if (this.state.top <= window.pageYOffset) {
        this.setState({
          top : window.pageYOffset + 5
        })
        clearInterval(id);
      }
    }, 10)
  }

  componentWillUnmount() {
    window.onscroll = () => {}
  }

  initDownAnimation() {
    let id = setInterval(() => {
      this.setState({
        top : this.state.top + 20
      });
      if (this.state.top >= window.pageYOffset + window.screen.height) {
        this.props.reset();
        clearInterval(id);
      }
    }, 10)
  }

  render() {
    const { user } = this.props.user === undefined ? {user:{}}: this.props;
    return (
      <div style={this.getPopUpStyle()} className='pop-up'>
        <img style={backImageStyle} onClick={() => this.initDownAnimation()} src={backImage} alt='' />
        <ul style={listStyle}>
          <li><span style={spanStyle}>{(user.firstName || '') + ' ' + (user.lastName || '')}</span></li>
          <li><img style={imageStyle} src={user.profileImage || ''} alt=''></img></li>
          <li><span style={spanStyle} className='info-heading'>Address</span></li>
          <li>{user.address ? user.address.city : ''}</li>
          <li>{user.address ? user.address.state : ''}</li>
          <li>{user.address ? user.address.country : ''}</li>
          <li>{user.address ? user.address.zipCode : ''}</li>
          <li>{user.address ? user.address.countryCode : ''}</li>
          <li>{user.address ? user.address.streetAddress : ''}</li>
          <li><span style={spanStyle}>Contact</span></li>
          <li>{user.email}</li>
          <li>{user.phone}</li>
        </ul>
      </div>
    )
  }
}

const listStyle = {
  display: 'flex',
  flexFlow: 'column wrap',
  alignItems: 'center',
  margin: '10px',
  padding: '5px',
  lineHeight: '25px'
}

const imageStyle = {
  borderRadius: '10px',
}

const backImageStyle = {
  padding:'10px',
  cursor:'pointer'
}

const spanStyle = {
  color: '#244646',
  lineHeight: '50px'
}

